/* tslint:disable: ordered-imports*/
import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

/* Module */
import { DetailsModule } from './details.module';

/* Containers */
import * as tablesContainers from './containers';

/* Guards */
import * as tablesGuards from './guards';
import { SBRouteData } from '@modules/navigation/models';
import { CoreService } from '@modules/services/base.service';


@NgModule({
    imports: [DetailsModule, RouterModule.forChild(DetailsRoutingModule.Route)],
    exports: [RouterModule],
})
export class DetailsRoutingModule {
    constructor(coreService: CoreService){
    }
    public static Data: SBRouteData = {
        title: localStorage.getItem('title')!,
        breadcrumbs: [
            {
                text: 'Dashboard',
                link: '/dashboard',
            },
            {
                text: 'Details',
                active: true,
            },
        ],
    }
    public static Route: Routes = [
        {
            path: '',
            canActivate: [],
            component: tablesContainers.TablesComponent,
            data: DetailsRoutingModule.Data,
            children: [
                {path: '', component: tablesContainers.TablesComponent, data: DetailsRoutingModule.Data},
                {
                    path: ':id',
                    component: tablesContainers.TablesComponent,
                    data: DetailsRoutingModule.Data,
                    children: [
                        {path: '', component: tablesContainers.TablesComponent, data: DetailsRoutingModule.Data},
                        {
                            path: ':op',
                            component: tablesContainers.TablesComponent,
                            data: DetailsRoutingModule.Data
                        }
                    ]
                }
            ]
        }
    ];
}
